* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background-color: #fee12b;
  font-family: "Share Tech Mono", monospace; }

.container {
  max-width: 1100px;
  margin: 0 auto; }

.header {
  width: 100%;
  text-align: center;
  padding: 25px 0; }
  .header h1 {
    display: inline-block;
    font-size: 25px;
    position: relative; }
    @media only screen and (min-width: 385px) {
      .header h1 {
        font-size: 35px; } }
    @media (min-width: 520px) {
      .header h1 {
        font-size: 50px; } }
    @media (min-width: 695px) {
      .header h1 {
        font-size: 60px; } }
    .header h1 img {
      position: absolute;
      top: 0;
      margin: 7px;
      width: 25px;
      height: 25px; }
      @media only screen and (min-width: 385px) {
        .header h1 img {
          margin: 10px;
          width: 35px;
          height: 35px; } }
      @media (min-width: 520px) {
        .header h1 img {
          width: 50px;
          height: 50px; } }
      @media (min-width: 695px) {
        .header h1 img {
          width: 60px;
          height: 60px; } }

.form-header {
  text-align: center;
  font-size: 16px;
  margin: 0 20px; }
  @media (min-width: 695px) {
    .form-header {
      font-size: 20px; } }

.form-element {
  margin: 20px; }

.form h3 {
  font-size: 15px; }
  @media (min-width: 695px) {
    .form h3 {
      font-size: 18px; } }

.form .buttons {
  margin: 10px auto;
  display: flex;
  flex-wrap: wrap; }

.form-submit {
  text-align: center; }
  .form-submit-button {
    outline: none;
    background: black;
    border: 3px solid black;
    border-radius: 5px;
    width: 70%;
    color: #fee12b;
    font-family: "Share Tech Mono", monospace;
    font-size: 14px;
    padding: 10px 15px;
    cursor: pointer; }
    .form-submit-button:hover {
      transform: scale(1.1); }
    @media (min-width: 520px) {
      .form-submit-button {
        width: 40%;
        font-size: 16px; } }
    @media (min-width: 695px) {
      .form-submit-button {
        font-size: 20px; } }

.button {
  padding: 10px 15px;
  border: 2px solid black;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  margin: 5px 5px; }
  .button:hover {
    transform: scale(1.1); }

input[type="radio"] {
  display: none; }

#all:checked + label,
#all-part:checked + label,
#all-prices:checked + label,
#all-access:checked + label {
  background-color: black;
  border-color: black;
  color: #fee12b; }

#recreational:checked + label {
  background-color: purple;
  border-color: purple;
  color: #fee12b; }

#education:checked + label {
  background-color: gray;
  border-color: gray;
  color: #fee12b; }

#relaxation:checked + label {
  background-color: green;
  border-color: green;
  color: #fee12b; }

#social:checked + label {
  background-color: deeppink;
  border-color: deeppink;
  color: #fee12b; }

#charity:checked + label {
  background-color: white;
  border-color: white;
  color: #fee12b; }

#busywork:checked + label {
  background-color: red;
  border-color: red;
  color: #fee12b; }

#cooking:checked + label {
  background-color: orange;
  border-color: orange;
  color: #fee12b; }

[name="participants"]:checked + label {
  background-color: dodgerblue;
  border-color: dodgerblue;
  color: #fee12b; }

[name="prices"]:checked + label {
  background-color: olive;
  border-color: olive;
  color: #fee12b; }

[name="accessibility"]:checked + label {
  background-color: darkblue;
  border-color: darkblue;
  color: #fee12b; }

.board {
  width: 90%;
  text-align: center;
  margin: 40px auto 20px auto;
  font-size: 18px;
  border: 3px solid black;
  border-radius: 5px;
  padding: 30px 20px;
  font-weight: 600;
  position: relative;
  min-height: 100px; }
  @media (min-width: 520px) {
    .board {
      font-size: 25px;
      border-width: 4px; } }

.signal {
  border: 5px solid #333;
  border-radius: 30px;
  height: 30px;
  left: 50%;
  margin: -15px 0 0 -15px;
  opacity: 0;
  position: absolute;
  top: 50%;
  width: 30px;
  animation: pulsate 1s ease-out;
  animation-iteration-count: infinite; }

@keyframes pulsate {
  0% {
    transform: scale(0.1);
    opacity: 0.0; }
  50% {
    opacity: 1; }
  100% {
    transform: scale(1.2);
    opacity: 0; } }
